export default {
     onShareAppMessage: function(ops) {
    if (ops.from === "button") {
      // 来自页面内转发按钮
      console.log(ops.target);
    }
    return {
      title: "大可爱",//这里是定义转发的标题
      path: `pages/index/index`,//这里是定义转发的地址
      success: function(res) {
        // 转发成功
        console.log("转发成功:" + JSON.stringify(res));
        var shareTickets = res.shareTickets;
      },
      fail: function(res) {
        // 转发失败
        console.log("转发失败:" + JSON.stringify(res));
      }
    };
  },
  data () {
    return {
        imgUrls:require('../../static/image/happy/753.jpg'),
        imgUrlInfo:require('../../static/image/happy/996.jpg'),
        heightChange:0,
        title:''
    }
  },
  components: {
  },

onLoad(){
    this.init();
  
  },
  methods: {
     
      init(){
          var urls='https://v1.alapi.cn/api/qinghua?type=json';
		  uni.request({
		  	url:urls,
			success: (res) => {
				this.title = res.data.data.content;
			}
		  })
      },
  
    
  }
}
